body{font-family:'Rock Salt', cursive;color:#f4f4f4;font-size:13px;line-height:1.5;letter-spacing:8}body>h1{font-size:46px;text-align:center}#layout{display:flex}#img{z-index:-1}.button{background-color:aquamarine;padding:1em 2em;color:black;border:0}.button:hover{background-color:blue}.modal{display:none;position:fixed;z-index:1;left:0;top:0;width:100%;height:100%;overflow:auto;background-color:rgba(0,0,0,0.5)}.modal-content{background-color:#f4f4f4;margin:20% auto;width:70%;box-shadow:0 5px 8px 0 rgba(0,0,0,0.3),0 7px 20px 0 #000;-webkit-animation-name:modalopen;animation-name:modalopen;-webkit-animation-duration:.5s;animation-duration:.5s}.close-btn{color:#ccc;float:right;font-size:30px}.close-btn:hover,.close-btn:focus{color:#000;text-decoration:none;cursor:pointer}@-webkit-keyframes modalopen{from{opacity:0}to{opacity:1}}@keyframes modalopen{from{opacity:0}to{opacity:1}}.modal-header{background:coral;padding:15px;color:#fff}.modal-header h2,.modal-footer h3{margin:0}.modal-body{padding:10px 20px}.modal-footer{background:coral;padding:10px;color:#fff;font-size:10px}#school-list{max-height:600px;overflow:auto;list-style:none;line-height:4;font-size:20px}#searches input{padding:6px;line-height:2}
